:root {
  --font-primary: "Pridi", Arial, sans-serif;
  --clr-text-grey: #393939;
  --clr-text-black: #0c0c0c;
  --clr-bg-footer: #4c4c4c;
  --clr-bg-grey: #f6f6f6;
  --clr-white-nonhover: #f2f2f2;
  --clr-accent: #f06c00;
  --clr-accent-opacity: hsla(27, 100%, 47%, 0.1);
  --clr-accent-hover: #e55000;
  --clr-accent-offer: #d10000;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: var(--clr-text-black);
}

a:hover,
a:focus {
  text-decoration: underline;
}

body {
  font-family: var(--font-primary);
  color: var(--clr-text-black);
  word-spacing: -0.125em;
  background-color: var(--clr-bg-grey);
}

li {
  list-style: none;
}

.headerMain {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: white;
  z-index: 2;
}

.headerRow {
  width: 98.5%;
  max-width: 1172px;
  min-width: 930px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* BODY WRAPPER -------------------------------*/
.bodyWrapper {
  width: 98.5%;
  max-width: 1172px;
  margin: 0 auto;
  margin-top: 155px;
  display: flex;
  justify-content: space-between;
}

.mainContentWrapper {
  max-width: 847px;
  width: 72%;
  min-width: 650px;
}

/* ----------------------------------------------*/
.headerRow:first-of-type {
  padding-top: 0.9em;
}

.headerLinkList {
  display: flex;
}

.headerLink {
  font-size: 0.8rem;
  color: var(--clr-text-grey);
  margin-right: 2.25em;
}

.logInCart {
  display: flex;
}

.logIn {
  margin-right: 3.5em;
  font-weight: bold;
}

.cart {
  display: inline-block;
  background-image: url(./img/icon-trolley.svg);
  height: 20px;
  width: 20px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  position: relative;
  margin-right: 5px;
}

.cart span {
  background-color: gray;
  position: absolute;
  color: white;
  font-size: 0.6rem;
  padding: 0 0.75em;
  right: -10px;
  top: -8px;
  border-radius: 40%;
}

.headerPrice {
  display: inline;
  padding: 0 0.25em;
}

.mainLogo img {
  margin-top: 20px;
  height: 28px;
  width: 140px;
}

.searchListOfItems {
  font-size: 0.8rem;
  text-decoration: underline;
  align-self: flex-end;
  margin-bottom: 0.5em;
  color: var(--clr-text-grey);
}

.navMain {
  padding-top: 0.45em;
  display: flex;
  font-size: 1.075rem;
  padding-bottom: calc(0.275em + 4px);
}

.navItem {
  margin-right: 1.3em;
  padding-bottom: 0.275em;
}

.navItem:hover,
.navItem:focus {
  text-decoration: none;
  border-bottom: 4px solid var(--clr-accent);
}

.navGroceries:hover,
.navGroceries:focus {
  text-decoration: none;
  border-bottom: none;
}

.navGroceriesText {
  border-bottom: 4px solid var(--clr-accent);
  padding-bottom: 0.275em;
}

.navArrow {
  background-image: url(./img/icon-arrow-down.svg);
  width: 12px;
  height: 12px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  display: inline-block;
}

.navGroceries:hover .navArrow,
.navGroceries:focus .navArrow {
  background-image: url(./img/icon-arrow-up.svg);
}

.navOffers {
  color: var(--clr-accent-offer);
}

.searchForm {
  display: flex;
  align-items: center;
  align-self: flex-start;
  position: relative;
}
.searchInput {
  width: 192px;
  height: 38px;
  padding: 0.25em 1.75em 0.25em 0.5em;
  font-size: 1.075rem;
  border: 1px solid var(--clr-text-grey);
}

.searchButton {
  margin-left: -4px;
  height: 38px;
  width: 40px;
  border: none;
  background-color: var(--clr-accent);
  cursor: pointer;
  background-image: url(./img/icon-search.png);
  background-position: center;
  background-repeat: no-repeat;
}

.clearButton {
  height: 12px;
  width: 10px;
  border: none;
  position: absolute;
  right: 21%;
  background-color: transparent;
  background-image: url(./img/icon-cross.png);
  background-repeat: no-repeat;
}

.searchButton:hover,
.searchButton:focus {
  background-color: var(--clr-accent-hover);
}

.carouselContainer {
  position: relative;
  height: 360px;
  /*   max-width: 847px; */
  width: 100%;
  /*  min-width: 650px; */
}

.carouselNavButtonsContainer {
  display: flex;
  justify-content: space-between;
  position: absolute;
  width: 11%;
  right: 50%;
  transform: translateX(50%);
  bottom: 3%;
}
.carouselContainer > button {
  position: absolute;
}
.carouselImage {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center;
}

.carouselNavButton {
  border: 2px solid var(--clr-text-grey);
  border-radius: 50%;
  width: 13px;
  height: 13px;
  cursor: pointer;
  background-color: transparent;
  cursor: pointer;
}

.carouselNavButtonActive,
.carouselNavButton:hover,
.carouselNavButton:focus {
  background-color: var(--clr-text-grey);
}

.carouselLeft {
  background-image: url(./img/icon-arrow-previous.svg);
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.carouselLeft:hover,
.carouselLeft:focus {
  background-image: url(./img/icon-arrow-previous-hover.svg);
}

.carouselRight {
  background-image: url(./img/icon-arrow-next.svg);
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.carouselRight:hover,
.carouselRight:focus {
  background-image: url(./img/icon-arrow-next-hover.svg);
}

.carouselSideButton {
  background-color: transparent;
  border: none;
  width: 40px;
  height: 40px;
  position: absolute;
  background-size: cover;
  background-repeat: no-repeat;
  cursor: pointer;
}

.carouselInfo {
  height: 56%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  right: 0;
  width: 46%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: var(--clr-text-grey);
}

.carouselHeading {
  font-size: 1.9rem;
  font-weight: 500;
  width: 14ch;
  line-height: 1.05em;
}

.carouselParagraph {
  width: 29ch;
  font-size: 1.05rem;
  font-weight: lighter;
  line-height: 1.25em;
}

.carouselInfoLight {
  color: white;
}

.carouselShopNowButton:hover,
.carouselShopNowButton:focus {
  text-decoration: none;
}

.carouselShopNowButton {
  margin-bottom: 10px;
  font-size: 1.125rem;
  font-weight: 500;
  background-color: var(--clr-accent);
  width: fit-content;
  padding: 0.05em 1.8em;
  color: white;
}

.headingMain {
  font-size: 1.62rem;
  margin: 1em 0;
}

.cardSmallRow {
  width: 100%;
  justify-content: space-between;
  display: flex;
}

.cardSmall {
  position: relative;
  height: 127px;
  max-width: 200px;
  margin-right: 1em;
  width: 100%;
}

.cardSmall:last-of-type {
  margin-right: 0px;
}

.cardSmallImage {
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
}

.headingCardSmall {
  color: var(--clr-text-grey);
  font-size: 1.125rem;
  font-weight: 500;
  position: absolute;
  bottom: 0;
  margin-bottom: 0.55em;
  width: 100%;
  text-align: center;
}

.productDisplayHome {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 2em;
}

.productCardContainer {
  text-align: center;
  font-weight: 300;
  font-size: 0.825rem;
  max-width: 200px;
  background-color: white;
  padding: 0.5em 0.8em 2em 0.8em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-right: 1em;
  width: 100%;
}

.productCardContainer:last-of-type {
  margin-right: 0;
}

.productCardImage {
  display: block;
  width: 85%;
  margin: 1.5em auto;
  object-fit: cover;
}

.productCardNameText {
  line-height: 1.2em;
}

.productCardPriceText {
  color: var(--clr-text-black);
  font-size: 1rem;
}

.productCardSavingText {
  color: var(--clr-accent-offer);
  font-size: 0.925rem;
}

.productCardSponsoredText {
  font-size: 1.125rem;
}

.productCardPriceText span {
  font-size: 1.3rem;
  font-weight: 800;
}

.productCardButton {
  color: white;
  width: 100%;
  padding: 0.75em 0;
  margin: 0.7em 0;
  font-size: 1.125rem;
  font-weight: 600;
  border: none;
  background-color: var(--clr-accent);
  cursor: pointer;
}

.productCardButton:hover,
.productCardButton:focus {
  background-color: var(--clr-accent-hover);
}

.offersCardRow {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2em;
}

.offersCardRow:last-of-type {
  margin-bottom: 0;
}

.offersCardContainer {
  max-width: 416px;
  height: 300px;
  margin-right: 1em;
  position: relative;
}

.offersCardContainer:last-of-type {
  margin-right: 0;
}

.offersCardImage {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.offersCardText {
  position: absolute;
  width: fit-content;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  margin-bottom: 1.6em;
  color: var(--clr-text-grey);
}

.offersCardHeading {
  font-size: 1.5rem;
  font-weight: 500;
  line-height: 1.2em;
}

.offersCardLink {
  font-weight: 600;
  text-decoration: underline;
}

.offersCardLink:hover,
.offersCardLink:focus {
  text-decoration: none;
}

.cardMediumRow {
  display: flex;
  justify-content: space-between;
  margin: 2em 0;
}

.cardMediumContainer {
  height: 312px;
  max-width: 416px;
  position: relative;
  margin-right: 1em;
}

.cardMediumContainer:last-of-type {
  margin-right: 0;
}

.cardMediumImage {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.cardMediumContent {
  position: absolute;
  bottom: 0;
  width: fit-content;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  margin-bottom: 0.5em;
}

.cardMediumHeading {
  color: var(--clr-text-grey);
  font-size: 1.575rem;
  font-weight: 700;
}

.cardMediumHeadingLight {
  color: white;
}

.cardMediumButton {
  border-radius: 2px;
  letter-spacing: -0.045em;
  font-size: 0.9rem;
  font-weight: 800;
  border: none;
  padding: 0.75em 1em;
  color: white;
  background-color: var(--clr-accent);
  cursor: pointer;
}

.cardMediumButton:hover,
.cardMediumButton:focus {
  background-color: var(--clr-accent-hover);
}

.cardMediumButtonLight {
  color: var(--clr-text-grey);
  background-color: var(--clr-white-nonhover);
}

.cardMediumButtonLight:hover,
.cardMediumButtonLight:focus {
  background-color: white;
}

.footerMain {
  margin: 0 auto;
  margin-top: 75px;
  width: 100%;
  left: 0;
  background-color: var(--clr-bg-footer);
}
.footerLinkList {
  background-color: var(--clr-bg-footer);
  justify-content: center;
  display: flex;
  font-size: 1.025rem;
  padding: 1.5em;
  width: 1172px;
  margin: 0 auto;
}

.footerLinkList a {
  color: white;
  font-weight: 300;
  margin-right: 1em;
}

.sidebarMain {
  width: 280px;
  margin-left: 1em;
}

.sidebarCard {
  padding: 1.25em 1em;
  background-color: white;
  margin-bottom: 1em;
}

.sidebarCard h1 {
  color: var(--clr-text-black);
  font-size: 1.1rem;
  margin-bottom: 1em;
}

.sidebarCard button {
  color: white;
  border: none;
  background-color: var(--clr-accent);
  padding: 0.35em 1.25em;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
}

.sidebarCard button:hover,
.sidebarCard button:focus {
  background-color: var(--clr-accent-hover);
}

.sidebarCard p {
  font-size: 0.9rem;
  font-weight: 400;
  line-height: 1.25em;
}

.linebreak {
  display: block;
  height: 1px;
  width: 278px;
  transform: translateX(-15px);
  background-color: rgb(190, 190, 190);
  margin-bottom: 1.25em;
}

.sidebarLogIn {
  margin-bottom: 1em;
}

.sidebarRegister {
  font-weight: 700;
  font-size: 1rem;
  text-decoration: underline;
}

.sidebarRegister:hover,
.sidebarRegister:focus {
  text-decoration: none;
}

.sidebarCard input {
  margin: 1em 0;
  font-size: 0.9rem;
  padding: 0.25em 1em;
  width: 179px;
}

.sidebarCreate {
  margin-top: 1em;
}

#trolley {
  overflow: hidden;
}
#trolley td button {
  color: var(--clr-accent-offer);
  padding: 0;
  padding: 0.25em;
  background-color: transparent;
}

.trolleyTable {
  color: var(--clr-text-grey);
  font-size: 0.75em;
  border-collapse: collapse;
}

.trolleyQuantity {
  font-size: 0.8rem;
  font-weight: 500;
}

thead {
  font-weight: 700;
  text-align: left;
  background-color: #e6e5e3;
  border-bottom: 1px solid var(--clr-accent-offer);
}

thead td {
  padding: 0.5em 1em;
}

tbody td {
  padding: 0.5em;
  background-color: #fff8ec;
}

.tablePriceData {
  font-size: 0.8rem;
  text-align: right;
  border-left: 1px solid #e6e5e3;
}

tfoot {
  text-align: right;
  font-size: 0.9rem;
  font-weight: 600;
}

tfoot td {
  color: var(--clr-text-black);
  padding-top: 0.5em;
}

#trolley .checkoutButton {
  float: right;
  margin-top: 0.75em;
}

.userForm {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0 auto;
  padding: 3em 1.5em 1.5em 1.5em;
  background-color: white;
}

.userForm label {
  display: flex;
  flex-direction: column;
  font-weight: 600;
  font-size: 1.1rem;
  margin-bottom: 0.5em;
}

.userForm input {
  font-size: 1rem;
  padding: 0.75em 1.25em;
  margin-top: 0.25em;
}

.sidebarDelete {
  margin-top: 1em;
}

.sidebarCard .accountEmail {
  font-size: 0.95rem;
  font-weight: 500;
}

.userFormContainer {
  margin: 0 auto;
  margin-top: 100px;
  width: 96%;
  max-width: 528px;
  color: var(--clr-text-grey);
}

.userFormContainer h1 {
  margin-bottom: 0.5em;
}

.userFormSubmitButton {
  text-align: center;
  width: 70%;
  margin: 0 auto;
  margin-top: 3em;
  cursor: pointer;
  padding: 0.8em 0;
  font-size: 1.1rem;
  font-weight: 700;
  color: white;
  border: none;
  background-color: var(--clr-accent);
}

.userFormSubmitButton:hover,
.userFormSubmitButton:focus {
  background-color: var(--clr-accent-hover);
}

.userFormRegisterText {
  text-align: center;
  font-weight: 300;
  margin-top: 1.5em;
  font-size: 1.1rem;
}

.userFormRegister {
  margin: 0 auto;
  margin-top: 1em;
  padding: 0.5em 0;
  text-align: center;
  width: 65%;
  border: 2px solid var(--clr-accent);
  color: var(--clr-accent);
  font-size: 1.1rem;
  font-weight: 600;
}

.userFormRegisterLink:focus,
.userFormRegisterLink:hover {
  text-decoration: none;
}
.userFormRegister:focus,
.userFormRegister:hover {
  background-color: var(--clr-accent-opacity);
}

.userFormError {
  color: var(--clr-accent-offer);
  font-weight: 600;
}
